
@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@500&display=swap');

.q{
    font-family: 'Comfortaa', cursive;
}

.show{

    transform: translateY(20px);
    opacity: 0;
    transition: all 0.3s ease-in-out;

}


.show.active{
    transform: translate(0);

    opacity: 1;

    
}